#resultsPanel {
	margin-top: 20px;
}

#resultsHeader {
	align-items: flex-start;
	display: flex;
	justify-content: space-between;
}

#resultsHeader > div > * + * {
	margin-left: 6px;
}

#resultsList {
	align-items: flex-start;
	display: flex;
	flex-wrap: wrap;
	margin-top: 10px;
	padding: 1px 0px 0px 1px;
}

#resultsList > div {
	border: solid 1px var(--item-border);
	display: flex;
	justify-content: space-between;
	margin: -1px 0px 0px -1px;
	padding: 2px 4px 2px 8px;
	width: 240px;
}

#resultsList > div > div {
	padding-right: 4px;
}

#resultsList > div i {
	cursor: pointer;
}

#resultsList > div i:hover {
	cursor: pointer;
	text-shadow: 0px 0px 5px lime;
}

@media screen and (max-width: 440px) {
	#resultsPanel {
		margin-top: 10px;
	}

	#resultsHeader h2 {
		display: none;
	}
}